*,
*::after,
*::before {
  box-sizing: border-box;
}

html {
  font-family: Inter, sans-serif;
  font-size: 20px;
  font-weight: 300;
}

body {
  margin: 0;
  color: #fff;
  background-color: #090910;
}

a {
  color: hsl(255, 100%, 60%);
  text-decoration: none;
}

section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 100px;
}
section .section-title {
  margin-bottom: 100px;
  text-transform: uppercase;
  font-size: 1.5rem;
  font-weight: 600;
}
section .section-title::after {
  content: "";
  display: block;
  width: 100%;
  height: 0.2rem;
  margin-top: 0.75rem;
  background-color: hsl(255, 100%, 60%);
}

header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
header .background-image {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.4;
  object-fit: cover;
  z-index: -1;
}
header .title,
header .description {
  margin: 0;
  text-align: center;
}
header .title {
  color: hsl(30, 100%, 65%);
  text-shadow: 0 0.2rem 1rem rgba(0, 0, 0, 0.2);
  font-family: Rubik, sans-serif;
  font-size: 6rem;
  font-weight: 600;
}
header .description {
  max-width: 60%;
  margin-top: 2rem;
  text-shadow: 0 0.2rem 1rem rgba(0, 0, 0, 0.5);
  line-height: 1.5;
  font-size: 1.5rem;
}
header .cta {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.75rem;
  margin-top: 6rem;
  padding: 0.75rem 1.5rem;
  color: inherit;
  border: 0.125rem solid #fff;
  border-radius: 1000000px;
  text-decoration: none;
  text-transform: uppercase;
  backdrop-filter: blur(4px);
  box-shadow: 0 0.2rem 1rem 0 rgba(0, 0, 0, 0.5);
  font-size: 1.25rem;
  transition: color 200ms ease, background-color 200ms ease;
}
header .cta:hover, header .cta:focus-visible {
  color: #000;
  background-color: #fff;
}
header .cta .text {
  margin: 0;
}
header .cta .icon {
  display: block;
  width: 1.3rem;
  height: 1.3rem;
}

.introductions {
  max-width: 1600px;
  margin: 0 auto;
}
.introductions .introduction {
  display: flex;
  gap: 4rem;
  padding: 2rem;
}
.introductions .introduction:nth-child(2n) {
  flex-direction: row-reverse;
}
.introductions .introduction .description {
  position: relative;
  width: 50%;
  margin: auto 0;
  padding: 2rem 0 2rem 1.5rem;
  line-height: 1.5;
}
.introductions .introduction .description::before {
  content: "";
  display: block;
  position: absolute;
  top: 2rem;
  bottom: 2rem;
  left: 0;
  width: 0.2rem;
  background-color: hsl(255, 100%, 60%);
}
.introductions .introduction .image {
  position: relative;
  width: 50%;
}
.introductions .introduction .image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 1rem;
}

.rules-container {
  width: 100%;
  max-width: 40%;
  margin: 0;
  padding: 0;
  border-radius: 1rem;
  list-style: none;
}
.rules-container .rule {
  border-radius: 1rem;
  transition: background-color 200ms ease;
}
.rules-container .rule.expanded {
  background-color: hsl(255, 100%, 60%);
}
.rules-container .rule.expanded .label .text {
  font-weight: 600;
}
.rules-container .rule.expanded .label:hover .icon {
  color: #fff;
}
.rules-container .rule.label-only .label .icon {
  color: #404040;
}
.rules-container .rule.label-only .label:hover {
  cursor: default;
}
.rules-container .rule.label-only .label:hover .icon {
  color: #404040;
}
.rules-container .rule .label {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
}
.rules-container .rule .label:hover {
  cursor: pointer;
}
.rules-container .rule .label:hover .icon {
  color: hsl(255, 100%, 60%);
}
.rules-container .rule .label .text {
  margin: 0;
  font-weight: 300;
  transition: font-weight 200ms ease;
}
.rules-container .rule .label .icon {
  display: block;
  width: 1.3rem;
  height: 1.3rem;
  transition: color 200ms ease;
}
.rules-container .rule .label .icon.hidden {
  display: none;
}
.rules-container .rule .body {
  height: 0;
  margin: 0;
  overflow: hidden;
  transition: height 200ms ease;
}
.rules-container .rule .body .text {
  margin: 0;
  padding: 0 1rem 1rem;
}

.rules-closing {
  width: 100%;
  max-width: 40%;
  margin-top: 2rem;
  line-height: 1.5;
  text-align: center;
}

.applications-text {
  max-width: 40%;
  text-align: center;
}

footer {
  padding: 100px;
}
footer .copyright {
  text-align: center;
}